import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { 
  Smartphone, 
  TrendingUp, 
  Shield, 
  BarChart3,
  ArrowLeft,
  CheckCircle,
  Star,
  Zap,
  Crown,
  LayoutDashboard,
  Monitor,
  Download
} from "lucide-react";
import BrandLogo from '../components/BrandLogo';
import { useEffect, useState } from 'react';
import { Link } from "react-router-dom";
import { useFirebaseAuth } from "@/contexts/FirebaseAuthContext";
import { useWhatsAppSettings, createWhatsAppLink } from '../utils/whatsappService';
import { getAppDownloads, getCachedOrEnvAppDownloads, listenAppDownloads, resolveDownloadUrl, type AppDownloads } from '@/utils/appDownloadService';

export default function LandingPage() {
  const { user } = useFirebaseAuth();
  const { settings } = useWhatsAppSettings();
  const [appDownloads, setAppDownloads] = useState<AppDownloads>(getCachedOrEnvAppDownloads());
  
  // SEO Optimization
  useEffect(() => {
    document.title = 'CashyLink | المنصة رقم 1 لإدارة المحافظ الإلكترونية ومحلات الموبايل';
    
    // Update Meta Description
    let metaDesc = document.querySelector('meta[name="description"]');
    if (!metaDesc) {
      metaDesc = document.createElement('meta');
      metaDesc.setAttribute('name', 'description');
      document.head.appendChild(metaDesc);
    }
    metaDesc.setAttribute('content', 'أفضل برنامج لإدارة محافظ فودافون كاش، اتصالات كاش، وأورنج كاش لمحلات الموبايل. تتبع المعاملات، حساب العمولات، وتقارير تفصيلية.');

    // Add Keywords
    let metaKeywords = document.querySelector('meta[name="keywords"]');
    if (!metaKeywords) {
      metaKeywords = document.createElement('meta');
      metaKeywords.setAttribute('name', 'keywords');
      document.head.appendChild(metaKeywords);
    }
    metaKeywords.setAttribute('content', 'إدارة محافظ إلكترونية, فودافون كاش, اتصالات كاش, أورنج كاش, برنامج حسابات محلات موبايل, CashyLink, تحويل رصيد, جرد المحافظ');

  }, []);

  // دالة لإنشاء رابط الواتساب للاستفسار العام
  const createGeneralWhatsAppLink = () => {
    if (!settings) return '#';
    
    const message = `مرحباً! أريد الاستفسار عن خدمات CashyLink لإدارة المحافظ الإلكترونية.

أرجو التواصل معي لمعرفة المزيد عن الباقات المتاحة وكيفية البدء.`;
    
    return createWhatsAppLink(settings.phoneNumber, message);
  };

  useEffect(() => {
    // جلب أولي (مرة واحدة)
    (async () => {
      try {
        const data = await getAppDownloads();
        setAppDownloads(data || {});
      } catch (e) {
        console.error('Failed to load app downloads', e);
      }
    })();
    // متابعة لحظية لتحديث الروابط فور تعديلها من لوحة الأدمن
    const unsub = listenAppDownloads((data) => {
      setAppDownloads(data || {});
    });
    return () => {
      try { unsub(); } catch {}
    };
  }, []);
  
  const features = [
    {
      icon: Smartphone,
      title: "إدارة الخطوط والمحافظ",
      description: "تحكم كامل في خطوط المحل ومحافظ التجار مع تتبع الحدود اليومية والشهرية"
    },
    {
      icon: TrendingUp,
      title: "نظام عمولة مرن",
      description: "احسب العمولة تلقائياً حسب قواعد قابلة للتخصيص لكل مزود وتاجر"
    },
    {
      icon: Shield,
      title: "أمان متقدم",
      description: "حماية البيانات مع صلاحيات متدرجة وسجل مراجعة شامل"
    },
    {
      icon: BarChart3,
      title: "تقارير تفصيلية",
      description: "تقارير يومية وشهرية قابلة للتصدير مع رسوم بيانية تفاعلية"
    }
  ];

  const providers = [
    { name: "Vodafone Cash", color: "bg-[#E60000]" },
    { name: "Etisalat Cash", color: "bg-[#72B842]" },
    { name: "Orange Cash", color: "bg-[#FF7900]" },
    { name: "InstaPay", color: "bg-[#4A148C]" }
  ];

  return (
    <div className="min-h-screen bg-[#fcf8f2] text-gray-900 font-sans" dir="rtl">
      {/* Header */}
      <header className="sticky top-0 z-50 w-full border-b bg-[#fcf8f2]/80 backdrop-blur-md supports-[backdrop-filter]:bg-[#fcf8f2]/60">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <div className="flex items-center gap-2">
            <BrandLogo className="object-contain" />
            <span className="text-xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-blue-600 to-indigo-600">
              CashyLink
            </span>
          </div>
          <nav className="hidden md:flex items-center gap-6">
            <a href="#features" className="text-sm font-medium text-gray-600 hover:text-primary transition-colors">المميزات</a>
            <a href="#download" className="text-sm font-medium text-gray-600 hover:text-primary transition-colors">التحميل</a>
            <Link to="/subscriptions" className="text-sm font-medium text-gray-600 hover:text-primary transition-colors">الأسعار</Link>
            <Link to="/privacy-policy" className="text-sm font-medium text-gray-600 hover:text-primary transition-colors">سياسة الخصوصية</Link>
          </nav>
          <div className="flex items-center gap-3">
            {!user ? (
              <>
                <Link to="/user/login">
                  <Button variant="ghost" className="text-gray-600 hover:text-primary hover:bg-blue-50">تسجيل الدخول</Button>
                </Link>
                <Link to="/user/register">
                  <Button className="bg-blue-600 hover:bg-blue-700 text-white shadow-sm">
                    تسجيل تاجر جديد
                  </Button>
                </Link>
              </>
            ) : (
              <Link to="/user/dashboard">
                <Button className="bg-blue-600 hover:bg-blue-700 text-white shadow-sm gap-2">
                  <LayoutDashboard className="h-4 w-4" />
                  لوحة التحكم
                </Button>
              </Link>
            )}
          </div>
        </div>
      </header>

      {/* Hero Section */}
      <section className="relative pt-20 pb-32 overflow-hidden bg-[#fcf8f2]">
        <div className="absolute inset-0 bg-[radial-gradient(#e5e7eb_1px,transparent_1px)] [background-size:16px_16px] opacity-30"></div>
        <div className="absolute inset-0 bg-gradient-to-b from-[#fcf8f2] via-transparent to-[#fcf8f2]/50"></div>
        
        <div className="container mx-auto px-4 relative z-10 text-center">
          <div className="inline-flex items-center gap-2 bg-blue-50 border border-blue-100 text-blue-700 px-4 py-1.5 rounded-full text-sm font-medium mb-8 animate-fade-in">
            <Star className="h-4 w-4 fill-blue-700" />
            <span>المنصة رقم #1 لإدارة المحافظ الإلكترونية</span>
          </div>
          
          <h1 className="text-5xl md:text-7xl font-extrabold tracking-tight text-gray-900 mb-6 max-w-4xl mx-auto leading-[1.15]">
            منصة إدارة شاملة <br />
            <span className="text-transparent bg-clip-text bg-gradient-to-r from-blue-600 to-indigo-600">
              لمحلات الموبايل
            </span>
          </h1>
          
          <p className="text-xl text-gray-600 mb-10 max-w-2xl mx-auto leading-relaxed">
            أدر معاملات الأموال الإلكترونية بكفاءة وأمان مع تتبع دقيق للحدود والعمولات، كل ذلك في منصة واحدة متكاملة.
          </p>

          <div id="download" className="flex flex-col items-center justify-center gap-4 mb-16 w-full max-w-md mx-auto scroll-mt-24">
            <div className="text-center mb-4">
              <h3 className="text-2xl font-bold text-gray-900">حمل التطبيق الآن</h3>
              <p className="text-gray-600">متاح لجميع الأنظمة والأجهزة</p>
            </div>

            {/* Android */}
            <a
              href={resolveDownloadUrl(appDownloads?.androidUrl) || '#'}
              target="_blank"
              rel="noopener noreferrer"
              className={`w-full flex items-center justify-between p-4 rounded-xl border-2 transition-all duration-200 ${appDownloads?.androidUrl ? 'border-gray-200 bg-white hover:border-green-500 hover:shadow-lg cursor-pointer' : 'border-gray-100 bg-gray-50 opacity-60 cursor-not-allowed'}`}
            >
              <div className="h-12 w-12 bg-green-100 text-green-600 rounded-full flex items-center justify-center">
                <Smartphone className="h-6 w-6" />
              </div>
              <div className="text-right flex-1 mr-4">
                <div className="text-xs text-gray-500">متوفر لنظام</div>
                <div className="text-lg font-bold text-gray-900">Android</div>
              </div>
              <div className="bg-green-50 text-green-700 px-3 py-1 rounded-full text-sm font-medium">
                تحميل
              </div>
            </a>

            {/* Windows 64 */}
            <a
              href={resolveDownloadUrl(appDownloads?.pcUrl) || '#'}
              target="_blank"
              rel="noopener noreferrer"
              className={`w-full flex items-center justify-between p-4 rounded-xl border-2 transition-all duration-200 ${appDownloads?.pcUrl ? 'border-gray-200 bg-white hover:border-blue-500 hover:shadow-lg cursor-pointer' : 'border-gray-100 bg-gray-50 opacity-60 cursor-not-allowed'}`}
            >
              <div className="h-12 w-12 bg-blue-100 text-blue-600 rounded-full flex items-center justify-center">
                <Monitor className="h-6 w-6" />
              </div>
              <div className="text-right flex-1 mr-4">
                <div className="text-xs text-gray-500">متوفر لنظام</div>
                <div className="text-lg font-bold text-gray-900">Windows (64-bit)</div>
              </div>
              <div className="bg-blue-50 text-blue-700 px-3 py-1 rounded-full text-sm font-medium">
                تحميل
              </div>
            </a>

            {/* Windows 32 */}
            <a
              href={resolveDownloadUrl(appDownloads?.pc32Url) || '#'}
              target="_blank"
              rel="noopener noreferrer"
              className={`w-full flex items-center justify-between p-4 rounded-xl border-2 transition-all duration-200 ${appDownloads?.pc32Url ? 'border-gray-200 bg-white hover:border-blue-500 hover:shadow-lg cursor-pointer' : 'border-gray-100 bg-gray-50 opacity-60 cursor-not-allowed'}`}
            >
              <div className="h-12 w-12 bg-blue-100 text-blue-600 rounded-full flex items-center justify-center">
                <Monitor className="h-6 w-6" />
              </div>
              <div className="text-right flex-1 mr-4">
                <div className="text-xs text-gray-500">متوفر لنظام</div>
                <div className="text-lg font-bold text-gray-900">Windows (32-bit)</div>
              </div>
              <div className="bg-blue-50 text-blue-700 px-3 py-1 rounded-full text-sm font-medium">
                تحميل
              </div>
            </a>

            <Link to="/subscriptions" className="mt-4">
              <Button variant="link" className="text-blue-600 hover:text-blue-800 font-bold">
                أو اشترك عبر الموقع مباشرة &larr;
              </Button>
            </Link>
          </div>

          <div className="flex flex-wrap justify-center gap-3 opacity-90">
            {providers.map((provider) => (
              <span 
                key={provider.name}
                className={`px-4 py-2 ${provider.color} text-white rounded-lg text-sm font-bold shadow-sm flex items-center gap-2`}
              >
                {provider.name}
              </span>
            ))}
          </div>
        </div>
      </section>

      {/* Stats Section */}
      <section className="py-12 bg-[#f9f5ed] border-y border-gray-200">
        <div className="container mx-auto px-4">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
            <div className="text-center p-6 bg-white rounded-2xl shadow-sm border border-gray-100">
              <div className="text-4xl font-bold text-blue-600 mb-2">500+</div>
              <div className="text-gray-600 font-medium">تاجر نشط</div>
            </div>
            <div className="text-center p-6 bg-white rounded-2xl shadow-sm border border-gray-100">
              <div className="text-4xl font-bold text-indigo-600 mb-2">1M+</div>
              <div className="text-gray-600 font-medium">معاملة شهرياً</div>
            </div>
            <div className="text-center p-6 bg-white rounded-2xl shadow-sm border border-gray-100">
              <div className="text-4xl font-bold text-green-600 mb-2">99.9%</div>
              <div className="text-gray-600 font-medium">وقت التشغيل</div>
            </div>
          </div>
        </div>
      </section>

      {/* Features Section */}
      <section id="features" className="py-24 bg-[#fcf8f2]">
        <div className="container mx-auto px-4">
          <div className="text-center mb-16">
            <h2 className="text-3xl md:text-4xl font-bold text-gray-900 mb-4">مميزات المنصة</h2>
            <p className="text-lg text-gray-600 max-w-2xl mx-auto">
              صمم خصيصاً لتلبية احتياجات أصحاب محلات الموبايل ونقاط البيع
            </p>
          </div>
          
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
            {features.map((feature, index) => (
              <Card key={index} className="border border-gray-100 shadow-sm hover:shadow-md transition-shadow duration-300 bg-[#f9f5ed]/50 hover:bg-white">
                <CardContent className="p-8 text-center">
                  <div className="h-14 w-14 bg-blue-100 text-blue-600 rounded-2xl flex items-center justify-center mx-auto mb-6">
                    <feature.icon className="h-7 w-7" />
                  </div>
                  <h3 className="text-xl font-bold text-gray-900 mb-3">{feature.title}</h3>
                  <p className="text-gray-600 leading-relaxed text-sm">
                    {feature.description}
                  </p>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
      </section>

      {/* Benefits / Why Us */}
      <section className="py-24 bg-gray-900 text-white relative overflow-hidden">
        <div className="absolute inset-0 bg-[url('/grid.svg')] opacity-10"></div>
        <div className="container mx-auto px-4 relative z-10">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-16 items-center">
            <div>
              <div className="inline-flex items-center gap-2 bg-blue-500/20 text-blue-300 px-4 py-1.5 rounded-full text-sm font-medium mb-6 border border-blue-500/30">
                <Zap className="h-4 w-4" />
                <span>لماذا نحن الأفضل؟</span>
              </div>
              <h2 className="text-3xl md:text-5xl font-bold mb-8 leading-tight">
                كل ما تحتاجه لإدارة <br />
                <span className="text-blue-400">ناجحة وآمنة</span>
              </h2>
              <div className="space-y-4">
                {[
                  "تتبع دقيق للحدود اليومية والشهرية",
                  "تنبيهات تلقائية عند اقتراب الحدود",
                  "حساب عمولة ذكي ومرن",
                  "تقارير شاملة قابلة للتصدير",
                  "واجهات منفصلة للإدارة والتجار",
                  "أمان متقدم مع صلاحيات متدرجة"
                ].map((benefit, index) => (
                  <div key={index} className="flex items-center gap-3">
                    <CheckCircle className="h-5 w-5 text-green-400 flex-shrink-0" />
                    <span className="text-gray-300 text-lg">{benefit}</span>
                  </div>
                ))}
              </div>
            </div>
            
            <div className="bg-white/10 backdrop-blur-lg rounded-3xl p-8 border border-white/10">
              <div className="flex items-center gap-4 mb-6">
                <div className="h-12 w-12 bg-blue-600 rounded-xl flex items-center justify-center">
                  <Crown className="h-6 w-6 text-white" />
                </div>
                <div>
                  <h3 className="text-xl font-bold">ابدأ تجربتك الآن</h3>
                  <p className="text-gray-400 text-sm">خطط مرنة تناسب جميع الاحتياجات</p>
                </div>
              </div>
              <p className="text-gray-300 mb-8 leading-relaxed">
                انضم إلى مئات التجار الذين يعتمدون على CashyLink يومياً لإدارة أعمالهم بكفاءة.
              </p>
              <Link to="/subscriptions">
                <Button className="w-full h-12 bg-white text-gray-900 hover:bg-gray-100 font-bold">
                  عرض الباقات والاشتراك
                </Button>
              </Link>
            </div>
          </div>
        </div>
      </section>



      {/* Footer */}
      <footer className="bg-[#fcf8f2] border-t border-gray-200 py-12">
        <div className="container mx-auto px-4">
          <div className="flex flex-col md:flex-row justify-between items-center gap-6">
            <div className="flex items-center gap-2">
              <BrandLogo size={24} className="text-gray-400 grayscale" />
              <span className="text-lg font-semibold text-gray-500">CashyLink</span>
            </div>
            <div className="text-sm text-gray-500">
              © {new Date().getFullYear()} جميع الحقوق محفوظة.
            </div>
            <div className="flex gap-6">
              <Link to="/privacy-policy">
                <Button variant="link" className="text-sm text-gray-500 hover:text-gray-900 p-0">
                  سياسة الخصوصية
                </Button>
              </Link>
              <a href="#" className="text-sm text-gray-500 hover:text-gray-900 flex items-center">الشروط والأحكام</a>
            </div>
          </div>
        </div>
      </footer>
    </div>
  );
}
